// src/features/gestionVentas/hooks/useVentas.ts

import { useState } from 'react';
import { VentaServicio, DetalleServicio, Cliente } from '@/entities/venta';

export const useVentas = () => {
  const [ventas, setVentas] = useState<VentaServicio[]>([]);

  const registrarVenta = (venta: VentaServicio) => {
    setVentas(prev => [...prev, venta]);
  };

  return {
    ventas,
    registrarVenta,
  };
};
