// src/app/dashboard/gestion-ventas/page.tsx
'use client';

import React from 'react';
import VentaForm from '../../components/ventas/VentaForm';
import VentasTable from '../../components/ventas/VentasTable';

const GestionVentasPage = () => {
  return (
    <main className="min-h-screen bg-gray-50 py-8 px-4 md:px-8">
      <div className="max-w-6xl mx-auto space-y-8">
        <header>
          <h1 className="text-3xl font-bold text-gray-800">Gestión de Ventas</h1>
          <p className="text-gray-600 mt-2">Registra nuevas ventas de servicios y visualiza el historial completo.</p>
        </header>

        {/* Formulario para registrar nueva venta */}
        <section className="bg-white p-6 rounded-xl shadow-md border border-gray-200">
          <h2 className="text-xl font-semibold text-gray-800 mb-4">Registrar Nueva Venta</h2>
          <VentaForm />
        </section>

        {/* Historial de ventas */}
        <section className="bg-white p-6 rounded-xl shadow-md border border-gray-200">
          <h2 className="text-xl font-semibold text-gray-800 mb-4">Historial de Ventas</h2>
          <VentasTable />
        </section>
      </div>
    </main>
  );
};

export default GestionVentasPage;
