// src/features/gestionVentas/components/VentasTable.tsx
'use client';
import React from 'react';
import { useVentas } from '../../../hooks/useVentas';
import { formatCurrency, formatDate } from '@/lib/logistica/formatters';

const VentaResumen = () => {
  const { ventas } = useVentas();

  const totalVentas = ventas.length;
  const totalIngresos = ventas.reduce((acc, venta) => acc + venta.costoTotal, 0);
  const totalDescuentos = ventas.reduce((acc, venta) => acc + venta.descuento, 0);

  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-4 my-6">
      <div className="bg-white p-4 rounded-lg shadow border">
        <h4 className="text-sm text-gray-500">Total Ventas</h4>
        <p className="text-2xl font-bold text-indigo-600">{totalVentas}</p>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <h4 className="text-sm text-gray-500">Ingresos Totales</h4>
        <p className="text-2xl font-bold text-green-600">{formatCurrency(totalIngresos)}</p>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <h4 className="text-sm text-gray-500">Descuentos Aplicados</h4>
        <p className="text-2xl font-bold text-red-600">{formatCurrency(totalDescuentos)}</p>
      </div>
    </div>
  );
};

const VentasTable = () => {
  const { ventas } = useVentas();

  return (
    <div>
      <VentaResumen />

      <div className="overflow-x-auto border border-gray-200 rounded-lg">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">ID Venta</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Cliente</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Fecha</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Total</th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Servicios</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {ventas.length > 0 ? (
              ventas.map((venta) => (
                <tr key={venta.idVentaServicio} className="hover:bg-gray-50">
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                    {venta.idVentaServicio}
                  </td>
                  <td className="px-6 py-4 text-sm text-gray-900">
                    {venta.cliente.nombre}
                  </td>
                  <td className="px-6 py-4 text-sm text-gray-500">
                    {formatDate(venta.fechaRegistro)}
                  </td>
                  <td className="px-6 py-4 text-sm font-semibold text-gray-900">
                    {formatCurrency(venta.costoTotal)}
                  </td>
                  <td className="px-6 py-4 text-sm text-gray-700">
                    {venta.detalles.map(d => d.nombreServicio).join(', ')}
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan={5} className="px-6 py-4 text-center text-sm text-gray-500">
                  No hay ventas registradas
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default VentasTable;
